/* ================================= Start Rating Css Start =========================== */
.rating-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px;

    @include lg-screen {
        gap: 7px;
    }

    @include msm-screen {
        gap: 5px
    }

    @include xsm-screen {
        gap: 2px
    }

    &__item {
        @extend .fs-18;
        color: hsl(var(--warning));

        @include msm-screen {
            @include fs-15;
        }

        @include xsm-screen {
            @include fs-12;
        }
    }
}

/* ================================= Start Rating Css End =========================== */