/* ===================== Scroll to Top Start ================================= */
.scroll-top {
    position: fixed;
    right: 20px;
    bottom: 20px;
    width: 45px;
    height: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    z-index: 5;
    transition: 0.5s;
    cursor: pointer;
    transform: scale(0);
    background-color: transparent;
    border: 1px solid hsl(var(--base));
    color: hsl(var(--base));
    @include fs-23;

    @include sm-screen {
        right: 15px;
        bottom: 15px;
        width: 40px;
        height: 40px;
        @include fs-18;
    }

    &:hover {
        color: hsl(var(--white));
        background-color: hsl(var(--base));
    }

    &.show {
        transform: scale(1);
    }
}

/* ===================== Scroll to Top End ================================= */