/* =========================== FAQ's Css Start ============================= */
.faq-content {
    padding-right: 50px;

    @include xl-screen {
        padding-right: 30px;
    }

    @include lg-screen {
        padding-right: 25px;
    }

    @include sm-screen {
        padding-right: 0;
        margin-bottom: 30px;
    }

    @include xsm-screen {
        margin-bottom: 20px;
    }

    &__title {
        max-width: 550px;
        margin-bottom: 35px;

        @include xl-screen {
            max-width: 500px;
        }

        @include lg-screen {
            max-width: 100%;
            margin-bottom: 20px;
        }
    }
}
/* =========================== FAQ's Css End ============================= */