/* ====================== Section Heading Start ==================== */
.section-heading {
  text-align: center;
  margin-bottom: 45px;

  @include lg-screen {
    margin-bottom: 35px;
  }

  @include md-screen {
    margin-bottom: 25px;
  }

  &__title {
    position: relative;
    margin-bottom: 0;
  }

  // Style left
  &.style-left {
    text-align: left;
  }
}

.section-link {
  position: relative;
  @extend .fs-20;
  font-weight: 600;
  margin-top: 35px;

  @include xl-screen {
    @include fs-18;
    margin-top: 20px;
  }

  @include lg-screen {
    @include fs-16;
  }

  @include sm-screen {
    margin-top: 10px;
  }

  &::after {
    position: absolute;
    content: '';
    background: hsl(var(--base));
    width: 100%;
    height: 2px;
    left: 0;
    bottom: 0;
  }
}

/* ====================== Section Heading End==================== */