/* ================================ About Section Start ============================= */
.about-section {
    position: relative;
}

.about-thumb {

    @include md-screen {
        text-align: center;
    }
}

.about-content {
    position: relative;
    z-index: 2;

    @include md-screen {
        margin-bottom: 30px;
    }

    &__title {
        padding-right: 30px;

        @include xl-screen {
            padding-right: 0;
        }
    }

    &__desc {
        padding-right: 40px;

        @include xl-screen {
            padding-right: 0;
        }
    }

    &__list {
        @extend .flex-wrap;
        margin-top: 35px;

        @include lg-screen {
            margin-top: 25px;
        }

        @include msm-screen {
            margin-top: 20px;
        }

        @include xsm-screen {
            display: block;
        }

        &-item {
            width: 50%;
            @extend .flex-align;
            margin-bottom: 40px;

            @include xl-screen {
                margin-bottom: 35px;
            }

            @include lg-screen {
                align-items: flex-start;
                margin-bottom: 20px;
            }

            @include xsm-screen {
                width: 100%;
                margin-bottom: 10px;
            }

            &:last-child,
            &:nth-last-child(2) {
                margin-bottom: 0;

                @include xsm-screen {
                    margin-bottom: 10px;
                }
            }

            .icon {
                width: 68px;
                height: 78px;
                background: hsl(var(--base));
                clip-path: polygon(46% 0, 54% 0, 100% 27%, 100% 72%, 54% 100%, 46% 100%, 0 72%, 0 26%);
                display: inline-flex;
                align-items: center;
                justify-content: center;
                color: hsl(var(--white));
                @include fs-40;

                @include xl-screen {
                    @include fs-32;
                    width: 60px;
                    height: 70px;
                }

                @include lg-screen {
                    @include fs-27;
                    width: 50px;
                    height: 60px;
                }

                @include sm-screen {
                    @include fs-23;
                    width: 45px;
                    height: 53px;
                }

                @include msm-screen {
                    @include fs-20;
                    width: 40px;
                    height: 47px;
                }
            }

            .content {
                width: calc(100% - 68px);
                padding-left: 20px;

                @include xl-screen {
                    width: calc(100% - 60px);
                    padding-left: 15px;
                }

                @include lg-screen {
                    width: calc(100% - 50px);
                }

                @include sm-screen {
                    width: calc(100% - 45px);
                }

                @include msm-screen {
                    padding-left: 10px;
                }

                .number {
                    color: hsl(var(--heading-color)/.30);
                    font-size: 3rem;
                    line-height: 1;
                    margin-bottom: 0;

                    @include xl-screen {
                        @include fs-40;
                    }

                    @include lg-screen {
                        @include fs-32;
                    }

                    @include sm-screen {
                        @include fs-28;
                    }

                    sub {
                        @include fs-32;
                        font-weight: 600;

                        @include xl-screen {
                            @include fs-28;
                        }

                        @include lg-screen {
                            @include fs-22;
                        }

                        @include sm-screen {
                            @include fs-18;
                        }
                    }
                }

                .desc {
                    @include fs-20;

                    @include xl-screen {
                        @include fs-17;
                    }

                    @include lg-screen {
                        @include fs-14;
                    }

                    @include sm-screen {
                        @include fs-13;
                    }
                }
            }
        }
    }
}

/* ================================ About Section End ============================= */