/* ================================= Form Css Start =========================== */
/* Form Label */
.form--label {
    margin-bottom: 10px;
    @include fs-18;
    color: hsl(0, 0%, 33%);

    @include xl-screen {
        @include fs-16;
    }

    @include lg-screen {
        @include fs-15;
        margin-bottom: 5px;
    }

    @include sm-screen {
        @include fs-14;
    }
}

.form-group {
    margin-bottom: 1rem;

    @include msm-screen {
        margin-bottom: 10px;
    }
}

/* Form Select */
.select {
    color: hsl(0, 0%, 33%) !important;

    &.form--control {
        padding: 17px 25px;

        @include xl-screen {
            padding: 15px;
        }

        @include lg-screen {
            padding: 13px 15px;
        }

        @include msm-screen {
            padding: 12px 13px;
        }

    }

    &:focus {
        border-color: hsl(var(--base));
        color: hsl(var(--black)) !important;
    }

    option {
        background-color: hsl(0, 0%, 93%);
        color: hsl(0, 0%, 33%);
    }
}

/* Form Select End */

/* Form Control Start */
.form--control {
    background-color: hsl(0, 0%, 93%) !important;
    border: 1px solid hsl(0, 0%, 93%);
    border-radius: 10px;
    color: hsl(0, 0%, 33%);
    @include fs-18;
    font-weight: 400;
    outline: none;
    width: 100%;
    padding: 18px 25px;
    line-height: 1;

    @include xl-screen {
        @include fs-16;
        padding: 16px 15px;
    }

    @include lg-screen {
        @include fs-15;
        padding: 14px 15px;
    }

    @include sm-screen {
        @include fs-14;
    }

    @include msm-screen {
        padding: 13px;
    }

    &::placeholder {
        color: hsl(0, 0%, 33%);
        @include fs-18;

        @include xl-screen {
            @include fs-16;
        }

        @include lg-screen {
            @include fs-15;
        }

        @include sm-screen {
            @include fs-14;
        }
    }

    &:focus {
        border-color: hsl(var(--base));
        box-shadow: none;
    }

    &:disabled,
    &[readonly] {
        background-color: hsl(var(--black)/.2);
        opacity: 1;
        border: 0;
    }

    &[type="password"] {
        color: hsl(var(--black)/.5);

        &:focus {
            color: hsl(var(--black));
        }
    }

    &[type="file"] {
        line-height: 50px;
        padding: 0;
        position: relative;

        &::file-selector-button {
            border: 1px solid hsl(var(--black) / .08);
            padding: 4px 6px;
            border-radius: .2em;
            background-color: hsl(var(--base)) !important;
            transition: .2s linear;
            line-height: 25px;
            position: relative;
            margin-left: 15px;
            color: hsl(var(--body-color)) !important;

            &:hover {
                background-color: hsl(var(--base));
                border: 1px solid hsl(var(--base));
                color: hsl(var(--black));
            }
        }
    }
}

/* Form Control End */

// Message Field Css
textarea.form--control {
    height: 130px;
}

/* Autofill Css */
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
textarea:-webkit-autofill:active {
    -webkit-transition: background-color 5000s ease-in-out 0s;
    transition: background-color 5000s ease-in-out 0s;
}

input:-webkit-autofill,
textarea:-webkit-autofill,
select:-webkit-autofill,
textarea:-webkit-autofill,
textarea:-webkit-autofill,
textarea:-webkit-autofill {
    -webkit-box-shadow: 0 0 0px 1000px transparent inset;
    -webkit-text-fill-color: hsl(var(--white)) !important;
    caret-color: hsl(var(--white));
}

/* Autofill Css End */

/* input group */
.input--group {
    position: relative;
}

/* Show Hide Password */
input#your-password,
input#confirm-password {
    padding-right: 50px;
}

.password-show-hide {
    position: absolute;
    right: 20px;
    z-index: 5;
    cursor: pointer;
    top: 50%;
    transform: translateY(-50%);
    color: hsl(var(--black)/.4);

    @include msm-screen {
        @include fs-14;
    }
}

/* --------------- Number Arrow None --------------------- */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

input[type=number] {
    -moz-appearance: textfield;
}

/* Custom Checkbox Design */
.form--check {
    @extend .flex-wrap;

    a {
        display: inline;
    }

    .form-check-input {
        box-shadow: none;
        background-color: transparent;
        box-shadow: none !important;
        border: 0;
        position: relative;
        border-radius: 4px;
        width: 16px;
        height: 16px;
        border: 1px solid hsl(var(--black)/.2);
        cursor: pointer;

        @include msm-screen {
            margin-top: 2px;
        }

        &:checked {
            background-color: hsl(var(--base)) !important;
            border-color: hsl(var(--base)) !important;
            box-shadow: none;

            &[type=checkbox] {
                background-image: none;
            }

            &::before {
                position: absolute;
                content: "\f00c";
                font-family: "Font Awesome 5 Free";
                font-weight: 900;
                color: hsl(var(--white));
                @extend .fs-11;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }
    }

    .form-check-label {
        width: calc(100% - 16px);
        padding-left: 12px;
        cursor: pointer;
        @include fs-16;

        @include lg-screen {
            @include fs-15;
            padding-left: 8px;
        }

        @include md-screen {
            @include fs-14;
        }

        @include msm-screen {
            @include fs-13;
        }
    }

    label {
        @include xsm-screen {
            @include fs-15;
        }
    }

    a {
        @include xsm-screen {
            @include fs-15;
        }
    }
}

/* Custom Radio Design */
.form--radio {
    @extend .flex-wrap;

    .form-check-input {
        box-shadow: none;
        border: 1px solid hsl(var(--black)/.2);
        position: relative;
        background-color: transparent;
        cursor: pointer;
        width: 16px;
        height: 16px;

        &:active {
            filter: brightness(100%);
        }

        &:checked {
            background-color: transparent;
            border-color: hsl(var(--base));

            &[type=radio] {
                background-image: none;
            }

            &::before {
                position: absolute;
                content: "";
                top: 50%;
                left: 50%;
                -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
                width: 7px;
                height: 7px;
                background-color: hsl(var(--base));
                border-radius: 50%;
                z-index: 999;
            }
        }
    }

    .form-check-label {
        font-weight: 600;
        width: calc(100% - 16px);
        padding-left: 12px;
        cursor: pointer;
        @extend .fs-14;
    }
}

/*  Custom Switch Design */
.form--switch {
    .form-check-input {
        border-radius: 3px;
        background-image: none;
        position: relative;
        box-shadow: none;
        border: 0;
        background-color: hsl(var(--black)/.2) !important;
        padding: 10px !important;
        margin-left: 0;
        margin-bottom: 5px;
        border-radius: 40px;
        width: 70px;
        height: 38px;
        cursor: pointer;

        &:focus {
            border-radius: 40px;
            background-image: none;
            position: relative;
            box-shadow: none;
            border: 0;
        }

        &::before {
            position: absolute;
            content: "";
            width: 28px;
            height: 28px;
            background-color: hsl(var(--white));
            top: 50%;
            transform: translateY(-50%);
            border-radius: 2px;
            left: 5px;
            border-radius: 50%;
            transition: 0.2s linear;
        }

        &:checked {
            background-color: hsl(var(--base)) !important;

            &::before {
                left: calc(100% - 33px);
                background-color: hsl(var(--white)) !important;
            }
        }

        &:checked[type=checkbox] {
            background-image: none;
        }
    }

    .form-check-label {
        width: calc(100% - 14px);
        padding-left: 5px;
        cursor: pointer;
    }
}

/*  Custom Switch End Design */
/* ================================= Form Css End =========================== */