/* ================================= Dashboard Fulid Sidebar Css Start =========================== */
.dashboard {

  // Sidebar logo 
  .sidebar-logo {
    text-align: center;
    margin: 24px 0;

    @include msm-screen {
      padding: 24px 0 15px;
    }

    img {
      max-width: 200px;
      max-height: 50px;

      @include lg-screen {
        max-width: 160px;
      }
    }
  }

  // Sidebar Menu
  .sidebar-menu {
    height: 100vh;
    background-color: hsl(var(--base));
    overflow-y: auto;
    z-index: 999;
    transition: 0.2s linear;
    width: 345px;
    border-radius: 0;
    position: fixed;
    left: 0;
    top: 0;
    align-items: start;
    flex-direction: row;

    @include xxxl-screen {
      width: 300px;
    }

    @include msm-screen {
      width: 270px;
    }

    &::-webkit-scrollbar {
      width: 3px;
      height: 3px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: hsl(var(--black)/.15);
    }

    &__inner {
      width: 100%;
    }

    //=========== Sidebar Show hide Css Start =======
    &.show-sidebar {
      transform: translateX(0);
    }

    @include lg-screen {
      transform: translateX(-100%);
      z-index: 9992;
      border-radius: 0;
    }

    &__close {
      position: absolute;
      top: 10px;
      right: 10px;
      color: hsl(var(--white)/.80);
      @include fs-20;
      line-height: 1;
      transition: 0.2s linear;
      cursor: pointer;
      z-index: 9;

      &:active {
        top: 14px;
      }

      &:hover,
      &:focus {
      background-color: hsl(var(--white));
        border-color: hsl(var(--white));
        color: hsl(var(--base));
      }
    }

    // List
    &-list {

      &__item {
        margin-bottom: 1px;

        &:last-child {
          .sidebar-menu-list__link {
            border-bottom: 0;
          }
        }

        &.active {
          >a {
            background-color: hsl(216, 100%, 38%);
            color: hsl(var(--white)/.80);
          }
        }

        &.has-dropdown {
          &.active {
            >a {
              color: hsl(var(--base));

              &:after {
                transform: rotate(90deg);
                right: 18px;
                color: hsl(var(--base));
              }
            }
          }

          >a {
            &:after {
              position: absolute;
              content: "\f105";
              font-family: "Font Awesome 5 Free";
              font-weight: 900;
              font-style: normal;
              display: inline-block;
              font-style: normal;
              font-variant: normal;
              text-rendering: auto;
              text-align: center;
              background: 0 0;
              right: 16px;
              top: 14px;
              transition: 0.1s linear;
              color: hsl(var(--body-color) / 0.6);
              @extend .fs-13;
            }
          }
        }
      }

      &__link {
        @extend .flex-align;
        text-decoration: none;
        position: relative;
        padding: 25px 10px 25px 35px;
        width: 100%;
        color: hsl(var(--white)/.80);
        @include fs-19;
        line-height: 1;

        @include xxxl-screen {
          @include fs-17;
          padding: 21px 10px 21px 25px;
        }

        @include sm-screen {
          @include fs-16;
          padding: 18px 10px 18px 20px;
        }

        @include msm-screen {
          @include fs-15;
          padding: 18px 10px 18px 20px;
        }

        &:hover {
          background-color: hsl(216, 100%, 38%);
          color: hsl(var(--white)/.80);
        }

        .icon {
          margin-right: 15px;

          @include xxxl-screen {
            margin-right: 10px;
          }
        }
      }
    }
  }


  // Sidebar Submenu
  .sidebar-submenu {
    display: none;

    &.open-submenu {
      display: block;
    }

    &-list {
      padding: 5px 0;

      &__item {
        margin-bottom: 6px;

        &.active {
          >a {
            color: hsl(var(--base));
            background-color: hsl(var(--base)/0.06);
          }
        }
      }

      &__link {
        padding: 12px 15px;
        display: block;
        color: hsl(var(--body-color));
        color: #6b717e;
        font-weight: 500;
        @extend .fs-15;
        margin-left: 20px;
        border-radius: 5px;
        position: relative;
        padding-left: 25px;

        &::before {
          @extend .pa-extend;
          left: 10px;
          @extend .top-center-extend;
          width: 10px;
          height: 10px;
          background-color: transparent;
          border: 1px solid hsl(var(--black) / 0.4);
          border-radius: 50%;

        }

        &:hover {
          background-color: hsl(var(--base)/0.04);
        }

        .icon {
          margin-right: 8px;
          @extend .fs-15;
          text-align: center;
          border-radius: 4px;
        }
      }
    }
  }
}


/* ================================= Dashboard Fulid Sidebar Css End =========================== */