/* ================================= Common Typography Css Start =========================== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: var(--body-font);
  color: hsl(var(--body-color));
  word-break: break-word;
  background-color: hsl(var(--white));
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  line-height: 1.5;
}

p {
  @extend .fs-16;
  font-weight: 400;
  margin: 0;
}

span {
  display: inline-block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 15px 0;
  font-family: var(--heading-font);
  color: hsl(var(--heading-color));
  line-height: 1.28;

  @include lg-screen {
    margin: 0 0 10px 0;
  }

  @include msm-screen {
    margin: 0 0 5px 0;
  }
}

h1 {
  font-size: clamp(1.375rem, 0.1327rem + 3.3694vw, 3rem); // Min: 24px - Max: 50px
  font-weight: 800;
}

h2 {
  font-size: clamp(1.5rem, 0.1327rem + 2.7vw, 3rem); // Min: 24px - Max: 50px
  font-weight: 700;
}

h3 {
  font-size: clamp(1.5rem, 0.3353rem + 2.1661vw, 2.5rem); // Min: 22px - Max: 40px
  font-weight: 500;
}

h4 {
  font-size: clamp(1.25rem, 0.5569rem + 1.444vw, 2rem); // Min: 20px - Max: 32px
  font-weight: 500;
}

h5 {
  font-size: clamp(1rem, 0.7784rem + 0.722vw, 1.5rem); // Min: 18px - Max: 24px
  font-weight: 600;
}

h6 {
  font-size: clamp(0.875rem, 0.769rem + 0.4813vw, 1.125rem); // Min: 14px - Max: 18px
  font-weight: 600;
}

h1>a,
h2>a,
h3>a,
h4>a,
h5>a,
h6>a {
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
  color: inherit;
  transition: .2s linear;
}

a {
  display: inline-block;
  transition: .2s linear;
  text-decoration: none;
  color: hsl(var(--base));
}

a:hover {
  color: hsl(var(--base-d-200));
}

img {
  max-width: 100%;
  height: auto;
}

select {
  cursor: pointer;
}

ul,
ol {
  padding: 0;
  margin: 0;
  list-style: none;
}

button {
  border: 0;
  background-color: transparent;
  transition: .2s linear;

  &:focus {
    outline: none;
    box-shadow: none;
  }
}

.form-select:focus {
  outline: 0;
  box-shadow: none;
}

/* ================================= Common Typography Css End =========================== */