/* ============= Header Start Here ======================= */
.navbar-brand {
    padding-top: 0;
    padding-bottom: 0;

    &.logo {
        img {
            max-width: 200px;
            max-height: 50px;

            @include xl-screen {
                max-width: 165px;
            }

            @include md-screen {
                max-width: 150px;
            }
        }
    }
}

.header {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 5;
    background-color: transparent;

    @include md-screen {
        top: 0px;
        background-color: hsl(var(--white));
        padding: 10px 0;
        position: absolute;
        left: 0;
        right: 0;
        z-index: 999;
        max-height: 101vh;
        overflow-y: auto;

        &::-webkit-scrollbar {
            width: 5px;
            height: 5px;
        }

        &::-webkit-scrollbar-thumb {
            border-radius: 0px;
        }
    }

    &.fixed-header {
        background-color: hsl(var(--white));
        box-shadow: var(--box-shadow);
        position: fixed;
        transition: .3s linear;
        top: 0px;
        animation: slide-down .8s;
        width: 100%;
    }
}

@keyframes slide-down {
    0% {
        opacity: 0;
        transform: translateY(-150%);
    }

    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.navbar {
    padding: 0 !important;
}

/* ========================= Desktop Device Start ========================= */
.header {
    @media (min-width: 992px) {
        .nav-menu {
            padding-top: 0;
            padding-bottom: 0;

            .nav-item {
                position: relative;
                margin-right: 30px;

                @include xl-screen {
                    margin-right: 22px;
                }

                @include lg-screen {
                    margin-right: 20px;
                }

                &:last-child {
                    margin-right: 0;
                }

                &.active {
                    .nav-link {
                        color: hsl(var(--base)) !important;

                        &::before {
                            width: 100%;
                        }
                    }
                }

                &:hover {
                    .nav-link {
                        color: hsl(var(--base)) !important;

                        &::before {
                            width: 100%;
                        }

                        .nav-item__icon {
                            transform: rotate(180deg);
                            transition: .2s;
                        }
                    }
                }

                .nav-link {
                    font-weight: 600;
                    @include fs-18;
                    color: hsl(var(--heading-color)) !important;
                    padding: 40px 0;
                    position: relative;
                    cursor: pointer;

                    @include xl-screen {
                        @include fs-17;
                        padding: 35px 0;
                    }

                    @include lg-screen {
                        @include fs-16;
                        padding: 35px 0;
                    }

                    /* ======================== Style two ================ */
                    &:hover::before {
                        left: 0;
                        transition: .3s;
                    }

                    &::before {
                        position: absolute;
                        content: "";
                        right: 0;
                        bottom: 25px;
                        width: 0;
                        height: 2px;
                        background-color: hsl(var(--base));
                        // transform: translateX(-50%);
                        transition: .3s;
                    }

                    .nav-item__icon {
                        transition: .3s;
                        @include fs-13;
                        margin-left: 2px;

                        @include md-screen {
                            margin-right: 6px;
                        }
                    }
                }
            }
        }

        .dropdown-menu {
            display: block;
            visibility: hidden;
            opacity: 0;
            transition: .3s;
            top: 100%;
            left: 0;
            padding: 0 !important;
            transform: scaleY(0);
            transform-origin: top center;
            transition: .3s;
            overflow: hidden;
            border-radius: 0;
            min-width: 190px;

            &__list {
                border-bottom: 1px solid hsl(var(--black) / .08);

                &:last-child {
                    border-bottom: 3px solid hsl(var(--base));
                }
            }

            &__link {
                padding: 7px 20px;
                font-weight: 500;
                @include fs-16;
                transition: .3s;

                &:focus,
                &:hover {
                    color: hsl(var(--white));
                    background-color: hsl(var(--base));

                }
            }
        }

        .nav-menu {
            .nav-item {
                &:hover {
                    .dropdown-menu {
                        visibility: visible;
                        opacity: 1;
                        top: 100% !important;
                        transform: scaleY(1);
                    }
                }
            }
        }
    }
}
/* ========================== Desktop Device End ========================= */

/* ============================== Small Device ======================= */
.header {
    @include md-screen {
        .nav-menu {
            margin-top: 5px;

            .nav-item {
                text-align: left;
                display: block;
                position: relative;
                margin: 0;

                &:hover .nav-link .nav-item__icon {
                    transform: rotate(0deg) !important;
                }
            }
        }

        .nav-item {
            
            .nav-link {
                padding: 10px 0 !important;
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin: 0 !important;
                border-bottom: 1px solid hsl(var(--heading-color) / .2);
                color: hsl(var(--heading-color));

                @include msm-screen {
                    @include fs-15;
                    padding: 9px 0 !important;
                }

                &::before {
                    display: none;
                }

                &.show {
                    &[aria-expanded="true"] {
                        color: hsl(var(--base)) !important;

                        i {
                            transform: rotate(180deg);
                        }
                    }
                }
            }

            &:first-child {
                .nav-link {
                    padding-top: 0 !important;
                }
            }

            &:first-child>a {
                padding-top: 0 !important;
            }

            &:last-child>a{
                border-bottom: 0;
            }

            &.active {
                .nav-link {
                    color: hsl(var(--base));
                }
            }
        }

        .dropdown-menu {
            box-shadow: none;
            border-radius: 2px;
            width: 100%;
            margin: 0px !important;
            padding: 0 !important;
            border: 0;
            background-color: inherit;
            overflow: hidden;

            li {
                &:nth-last-child(1) {
                    border-bottom: none;
                }

                .dropdown-item {
                    padding: 10px 0px;
                    font-weight: 500;
                    @include fs-16;
                    color: hsl(var(--white));
                    border-bottom: 1px solid hsl(var(--white) / .2);
                    margin-left: 20px;
                    color: hsl(var(--white));

                    &:hover,
                    &:focus {
                        background-color: transparent;
                    }
                }
            }
        }
    }

    .navbar-toggler {
        &.header-button {
            border-color: transparent;
            color: hsl(var(--base));
            background: transparent !important;
            padding: 0 !important;
            border: 0 !important;
            border-radius: 0 !important;
            transition: .15s ease-in-out;
            width: auto;
            font-size: 2.5rem;

            @include sm-screen {
                font-size: 2.3rem;
            }

            @include msm-screen {
                font-size: 2rem;
            }

            &:focus {
                box-shadow: none !important;
            }

            &[aria-expanded="true"] i::before {
                content: "\f00d";
            }
        }
    }
}

@include md-screen {
    .body-overlay.show {
        visibility: visible;
        opacity: 1;
    }
}

.header-access-button {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 30px;

    @include xl-screen {
        gap: 20px;
    }

    @include lg-screen {
        gap: 15px;
    }

    .btn {
        @include md-screen {
            width: 100%;
            margin-top: 10px;
        }
    }

    .account-btn {
        width: 36px;
        height: 36px;
        padding: 5px;
        @include fs-18;
        display: inline-flex;
        align-items: center;
        justify-content: center;

        @include lg-screen {
            width: 34px;
            height: 34px;
            @include fs-16;                      
        }

        @include md-screen {
            @include fs-14;
            width: 32px;
            height: 32px;
        }
    }
}

/* ================================= Header Css End =========================== */