/* ================================= Button Css Start =========================== */
.btn-check:checked+.btn,
.btn.active,
.btn.show,
.btn:first-child:active,
:not(.btn-check)+.btn:active {
    color: none;
    background-color: none;
    border-color: none;
}
.pill {
    border-radius: 52px !important;
}

.rounded {
    border-radius: 50% !important;
}

.btn {
    color: hsl(var(--white)) !important;
    font-family: var(--body-font);
    font-weight: 600;
    border: 1px solid transparent;
    border-radius: 5px;
    position: relative;
    z-index: 1;
    padding: 17px 15px;
    @extend .fs-16;
    line-height: 1;

    @include xl-screen {
        padding: 16px 12px;
    }

    @include lg-screen {
        @include fs-15;
        padding: 15px 10px;
    }

    @include md-screen {
        @include fs-14;
    }

    @include sm-screen {
        padding: 14px 25px;
        @include fs-14;
    }

    @include msm-screen {
        @include fs-13;
        padding: 14px 10px;
    }


    &:hover,
    &:focus,
    &:focus-visible {
        box-shadow: none !important;
    }

    &:active {
        top: 1px;
    }

    &--lg {
        padding: 20px 35px;

        @include md-screen {
            padding: 18px 30px;
        }

        @include sm-screen {
            padding: 16px 25px;
        }
    }

    &--sm {
        padding: 10px 16px;
        @include fs-14;

        @include sm-screen {
            padding: 8px 12px;
        }
    }

    &--icon {
        width: 35px;
        height: 35px;
        line-height: 35px;
        @extend .fs-15;
        padding: 0;
    }

    .icon {
        margin-right: 5px;
    }

    // ============= Different Color Button Start ==================
    &--base {
        background-color: hsl(var(--base)) !important;

        &:hover,
        &:focus &:focus-visible {
            background-color: hsl(var(--base-d-200)) !important;
            border: 1px solid hsl(var(--base-d-200)) !important;
        }
    }

    &-outline--base {
        background-color: transparent !important;
        border: 1px solid hsl(var(--base)) !important;
        color: hsl(var(--base)) !important;

        &:hover,
        &:focus &:focus-visible {
            background-color: hsl(var(--base)) !important;
            color: hsl(var(--white)) !important;
        }
    }

    &--primary {
