/* ================================ Video Section Start ============================= */
.video-content {
    @include sm-screen {
        margin-bottom: 30px;
    }
    @include xsm-screen {
        margin-bottom: 20px;
    }
}

.video-thumb {
    position: relative;

    img {
        width: 100%;
    }

    .play-btn {
        width: 95px;
        height: 95px;
        border-radius: 50%;
        background: hsl(var(--heading-color)/0.32);
        display: inline-flex;
        align-items: center;
        justify-content: center;
        color: hsl(var(--white));
        font-size: 1.75rem;
        line-height: 1;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -25%);
        animation: video_animation 1.5s linear infinite;

        @include xl-screen {
            width: 80px;
            height: 80px;
            font-size: 1.563rem;
            transform: translate(-50%, -20%);
        }

        @include lg-screen {
            width: 70px;
            height: 70px;
            font-size: 1.375rem;
        }

        @include msm-screen {
            width: 65px;
            height: 65px;
            font-size: 1.25rem;
        }

        @include xxsm-screen {
            width: 65px;
            height: 65px;
            font-size: 1.25rem;
        }

        @include xxsm-screen {
            width: 60px;
            height: 60px;
            font-size: 1.125rem;
        }
    }
}

/* ================================ Video Section End ============================= */


  
@keyframes video_animation {
    0% {
      box-shadow: 0 0 0 0 hsl(var(--heading-color)/.05), 0 0 0 10px hsl(var(--heading-color)/.05), 0 0 0 25px hsl(var(--heading-color)/.05);
    }
    100% {
        box-shadow: 0 0 0 10px hsl(var(--heading-color)/.05), 0 0 0 25px hsl(var(--heading-color)/.05), 0 0 0 40px hsl(var(--heading-color)/.0);
    }
}
