/* ================================ Pricing Section Start ============================= */
.pricing-item {
    text-align: center;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: var(--box-shadow-pricing);
    transition: .2s linear;

    &:hover {
        box-shadow: var(--box-shadow-pricing-hover);
    }

    &__heading {
        border-radius: 10px;
        overflow: hidden;
        padding: 30px 10px;

        @include sm-screen {
            padding: 25px 10px;
        }

        @include xxsm-screen {
            padding: 20px 10px;
        }

        &.bg-img {
            background-position: left top !important;
        }
    }

    &__category {
        color: hsl(var(--white));
        margin-bottom: 7px;
    }

    &__price {
        color: hsl(var(--white));
        margin-bottom: 0;

        sub {
            @include fs-16;
            font-weight: 400;
        }
    }

    &__content {
        text-align: center;
        padding: 30px 24px 40px;

        @include xl-screen {
            padding: 20px 15px 30px;
        }
        
        @include sm-screen {
            padding-bottom: 20px;
        }

        @include msm-screen {
            padding: 20px 35px;
        }

        @include xsm-screen {
            padding: 20px 15px;
        }
    }

    &__list {
        text-align: left;
        margin-bottom: 30px;

        @include xl-screen {
            margin-bottom: 20px;
        }

        
    }

    &__item {
        position: relative;
        padding-left: 12px;
        margin-bottom: 15px;

        @include xl-screen {
            @include fs-15;
            margin-bottom: 13px;
        }

        @include sm-screen {
            @include fs-14;
        }

        &:last-child {
            margin-bottom: 0;
        }

        &::after {
            position: absolute;
            content: '';
            background: hsl(var(--body-color));
            width: 7px;
            height: 7px;
            border-radius: 50%;
            left: 0;
            top: 8px;

            @include xl-screen {
                top: 7px;
            }

            @include md-screen {
                width: 6px;
                height: 6px;
                top: 8px;
            }
        }
    }

    .btn {
        @include sm-screen {
            width: 100%;
        }
    }
}

/* ================================ Pricing Section End ============================= */