/* ================================ Feature Section Start ============================= */
.feature-item {
    position: relative;
    background: hsl(var(--card-bg));
    border: 1px solid hsl(var(--black)/0.1);
    border-radius: 10px;
    padding: 35px 25px 40px;

    @include xl-screen {
        padding: 30px 15px 35px;
    }

    @include sm-screen {
        padding: 25px 10px;
    }

    &__icon {
        max-width: 64px;
        max-height: 57px;
        margin-bottom: 25px;

        @include xl-screen {
            max-width: 55px;
            margin-bottom: 20px;
        }

        @include sm-screen {
            max-width: 45px;
            margin-bottom: 10px;
        }

        img {
            width: 100%;
        }
    }

    &__title {
        margin-bottom: 8px;
        @include xl-screen {
            @include fs-16;
        }

        @include sm-screen {
            @include fs-15;
            margin-bottom: 5px;
        }
    }

    &__desc {
        @include xl-screen {
            @include fs-15;
        }
        @include sm-screen {
            @include fs-14;
        }
    }
}
/* ================================ Feature Section End ============================= */