/* ================================ Testimonails Section Css Start ============================= */
.testimonial-card {
    position: relative;
    background: hsl(var(--card-bg));
    border: 1px solid hsl(var(--black)/.10);
    border-radius: 10px;
    padding: 30px 30px 35px;

    @include lg-screen {
        padding: 25px 20px;
    }

    @include msm-screen {
        padding: 20px 15px 25px;
    }

    @include xsm-screen {
        padding: 25px 10px 20px;
    }

    &__top {
        @extend .flex-between;
        margin-bottom: 20px;

        @include msm-screen {
            margin-bottom: 15px;
        }

        @include xsm-screen {
            margin-bottom: 10px;
        }

        .rating-wrapper {
            @extend .flex-align;

            .text {
                margin-left: 15px;
                margin-bottom: 0;

                @include lg-screen {
                    margin-left: 10px;
                }

                @include xsm-screen {
                    @include fs-13;
                    margin-left: 5px;
                }
            }
        }
    }

    &__name {
        margin-bottom: 0;
        @include xsm-screen {
            @include fs-13;
            margin-left: 5px;
        }
    }

    &__desc {
        @extend .fs-18;
        line-height: 1.4;
    }
}

/* ================================ Testimonails Section Css End ============================= */