/* ================================ Benefit Section Start ============================= */
.benefit {
    padding-right: 70px;

    @include xl-screen {
        padding-right: 30px;
    }

    @include sm-screen {
        padding-right: 0;
        margin-bottom: 30px;
    }

    &__title {
        margin-bottom: 30px;

        @include lg-screen {
            margin-bottom: 15px;
        }

        @include md-screen {
            margin-bottom: 10px;
        }
    }

    &__desc {
        @extend .fs-18;
        margin-bottom: 40px;

        @include lg-screen {
            margin-bottom: 25px;
        }

        @include md-screen {
            @include fs-15;
        }

        @include sm-screen {
            @include fs-14;
        }
    }

    &__item {
        border-bottom: 1px solid hsl(var(--black)/.05);
        padding-bottom: 30px;
        margin-bottom: 30px;

        @include lg-screen {
            padding-bottom: 20px;
            margin-bottom: 20px;
        }

        @include sm-screen {
            padding-bottom: 15px;
            margin-bottom: 15px;
        }

        &:last-child {
            margin-bottom: 0;
        }

        .title {
            @include sm-screen {
                margin-bottom: 5px;
            }
        }

        .desc {
            @include md-screen {
                @include fs-14;
            }
        }
    }

    .section-link {
        @include md-screen {
            @include fs-14;
        }
    }
}

.benefit-thumb {
    position: relative;
    text-align: right;
    height: 100%;

    @include md-screen {
        padding-top: 30px;
    }

    @include sm-screen {
        padding-top: 0;
        text-align: center;
    }

    .one {
        border-radius: 10px;
        overflow: hidden;
        position: absolute;
        left: 0;
        bottom: -15px;

        @include lg-screen {
            max-width: 280px;
            bottom: 0;
        }

        @include md-screen {
            max-width: 190px;
        }

        @include sm-screen {
            position: relative;
            max-width: 100%;
            margin-bottom: 15px;
        }

        @include xsm-screen {
            margin-bottom: 10px;
        }
    }

    .two {
        border-radius: 15px;
        overflow: hidden;

        
        @include lg-screen {
            max-width: 330px;
        }

        
        @include md-screen {
            max-width: 230px;
        }

        @include sm-screen {
            max-width: 100%;
        }
    }
}

/* ================================ Benefit Section End ============================= */