/* =============================== Brand Section Css Start ======================= */
.client_slider {
  
  

  &:last-of-type {
    margin-bottom: 0;
  }

  img {
    max-width: 200px !important;

    @include lg-screen {
      max-width: 145px !important;
    }

    @include md-screen {
      max-width: 145px !important;
    }
  }

  .owl-stage {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  .owl-item {
    display: flex;
    justify-content: center;
  }

  .slick-slide {
    margin: 0 10px;
  }
}

/* =============================== Brand Section Css End ======================= */