/* ================================= Table Css Start =========================== */
/* Table Content Css start */
.customer {
	@include xxsm-screen {
		display: block;
		text-align: left;
	}

	&__thumb {
		width: 40px;
		height: 40px;
		border-radius: 50%;
		overflow: hidden;

		@include xxxxl-screen {
			width: 35px;
			height: 35px;
		}

		@include xsm-screen {
			width: 30px;
			height: 30px;
		}
	}

	&__content {
		width: calc(100% - 40px);
		padding-left: 15px;
		text-align: left;

		@include xxxxl-screen {
			width: calc(100% - 36px);
			padding-left: 10px;
		}

		@include xsm-screen {
			padding-left: 8px;
		}

		@include xxsm-screen {
			width: 100%;
			padding-left: 0px;
			padding-top: 5px;
		}
	}

	&__name {
		margin-bottom: 0;
		@include fs-18;
		font-weight: 400;
		color: hsl(var(--black)/0.7);

		@media (max-width: 1799px) {
			@include fs-16;
		}

		@include xxxxl-screen {
			@include fs-15;
		}

		@include md-screen {
			@include fs-14;
		}

		@include xsm-screen {
			@include fs-13;
		}
	}
}

.action-buttons {
	@extend .flex-align;
	gap: 10px;
	justify-content: flex-end;
}

.action-btn {
	@include fs-16;
}

.monitor-btn {
	color: hsl(var(--body-color));

	&:hover {
		color: hsl(var(--base));
	}
}

.edit-btn {
	color: hsl(var(--info));
	background-color: hsl(var(--info) / .08)
}

.delete-btn {
	color: hsl(var(--danger));
	background-color: hsl(var(--danger) / .08);
}

/* Table Content Css end */

/* Table Css Start */
.table {
	margin: 0;
	border-collapse: collapse;
	border-collapse: separate;
	border-spacing: 0px 10px;

	@include md-screen {
		border-spacing: 0px 6px;
	}

	@include sm-screen {
		border-spacing: 0;
	}

	thead {
		tr {
			th {
				text-align: center;
				padding: 15px 20px;
				color: hsl(216, 7%, 57%);
				font-family: var(--heading-font);
				font-weight: 400;
				border: 0;
				max-width: 170px;
				@include fs-18;
				background-color: transparent;

				@media (max-width: 1799px) {
					@include fs-16;
					padding: 15px;
				}

				@include xxxxl-screen {
					@include fs-15;
				}

				@include md-screen {
					padding: 13px;
					@include fs-14;
				}

				&:not(:first-child) {
					border-left: 0;
				}

				&:first-child {
					text-align: left;
				}

				&:last-child {
					text-align: right;
				}
			}
		}
	}

	tbody {
		border: 0 !important;
		background-color: hsl(var(--white));

		tr {
			td {
				text-align: center;
				vertical-align: middle;
				padding: 15px 20px;
				border-width: 1px;
				border: 0;
				font-family: var(--heading-font);
				color: hsl(216, 7%, 57%);
				background: hsl(0, 0%, 97%);
				max-width: 170px;
				@include fs-18;

				@media (max-width: 1799px) {
					@include fs-16;
					padding: 15px;
				}
				
				@include xxxxl-screen {
					@include fs-15;
				}

				@include md-screen {
					padding: 13px;
					@include fs-14;
				}

				@include xsm-screen {
					@include fs-13;
				}

				&::before {
					content: attr(data-label);
					font-family: var(--heading-font);
					@extend .fs-14;
					color: hsl(var(--dark-two));
					display: none;
					width: 45% !important;
					text-align: left;

					@include xsm-screen {
						@include fs-13;
					}
				}

				&:first-child {
					border-radius: 10px 0 0 10px;
					text-align: left;
				}

				&:last-child {
					border-radius: 0 10px 10px 0;
					text-align: right;
				}

				.date {
					color: hsl(var(--dark-two));
				}
			}
		}
	}
}

.table--responsive--md {
	@include sm-screen {
		thead {
			display: none;
		}

		tbody {
			tr {
				display: block;
				
				td {
					background: transparent !important;
					display: flex;
					align-items: center;
					justify-content: space-between;
					gap: 15px;
					text-align: right;
					padding: 10px 0;
					border: none;
					max-width: unset;
					
					&:last-child {
						border-radius: 0;
						border-bottom: 1px solid var(--border-color-two);
					}

					&:first-child {
						text-align: right;
						border-radius: 0;
					}

					&::before {
						display: block;
					}

					.customer {
						display: block;
					
						&__thumb {
							width: 30px;
							height: 30px;
							margin-left: auto;
						}
					
						&__content {
							width: 100%;
							padding-left: 0;
						}
					
						&__name {
							
						}
					}
				}

				&:last-child {
					td  {
						&:last-child {
							border-bottom: 0;
						}
					}
				}
			}
		}
	}

	@include sm-screen {
		tbody {
			tr {
				td {
					border: 0;
				}
			}
		}
	}
}

.table--responsive--lg {
	@include md-screen {
		thead {
			display: none;
		}

		tbody {
			tr {
				display: block;

				&:nth-child(even) {
					background-color: hsl(var(--black) / .02);
				}

				&:last-child td {
					border-bottom: 0;
				}

				td {
					display: flex;
					align-items: center;
					justify-content: space-between;
					gap: 15px;
					text-align: right;
					padding: 10px 15px;
					border: none;
					border-bottom: 1px solid hsl(var(--black) / .08);
					max-width: unset;

					&:last-child {
						border: none;
					}

					&:first-child {
						text-align: right;
						border-left: 0;
					}

					&::before {
						display: block;
					}
				}
			}
		}
	}

	@include md-screen {
		tbody {
			tr {
				td {
					border: none;
				}
			}
		}
	}
}

