/* ================================ Beneficial Section Start ============================= */
.beneficial-item {
    position: relative;
    background: hsl(var(--white));
    border-radius: 10px;
    padding: 12px 12px 15px;

    @include sm-screen {
        padding: 8px 8px 10px;
    }

    @include msm-screen {
        height: 100%;
    }

    &__thumb {
        border-radius: 10px;
        overflow: hidden;

        img {
            width: 100%;
        }
    }

    &__title {
        margin: 10px 0 0;

        @include xl-screen {
            @include fs-16;
        }

        @include sm-screen {
            @include fs-15;
        }
    }
}

/* ================================ Beneficial Section End ============================= */