/* ================================= Card Css Start =========================== */
.custom--card {
    border-radius: 15px;
    box-shadow: var(--box-shadow);
    background-color: hsl(var(--white));
    padding: 20px 30px;
    border: 0;

    @include xxxxl-screen {
        padding: 20px 20px
    }

    @include msm-screen {
        padding: 20px 15px;
    }

    .card-header {
        @extend .flex-between;
        background-color: transparent;
        border: 0;
        padding: 0;

        @include sm-screen {
            margin-bottom: 20px;
        }

        &__title {
            color: hsl(var(--dark-two));
            font-weight: 500;

            @media (max-width: 1799px) {
                @include fs-20;
            }

            @include md-screen {
                @include fs-18;
            }

            @include sm-screen {
                @include fs-16;
            }
        }
    }

    .card-body {
        background-color: hsl(var(--white));
        padding: 0;
        border-radius: 0;
    }

    .card-footer {
        padding: 0;
        background-color: transparent;
    }
}

/* ================================= Card Css End =========================== */