/* =========================================== Account Css Start =========================*/
.account {
    position: relative;
    min-height: 100vh;

    &__inner {
        display: flex;
    }

    &__left {
        position: relative;
        width: 50%;

        @include xl-screen {
            width: 45%;
        }

        @include lg-screen {
            width: 42%;
        }

        @include md-screen {
            width: 0;
            display: none;
        }
    }

    &__right {
        width: 50%;
        min-height: 100vh;

        @include xl-screen {
            width: 55%;
        }

        @include lg-screen {
            width: 58%;
        }

        @include md-screen {
            width: 100%;
        }
    }

    &__thumb {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: -2;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__content {
        background-color: hsl(var(--white));
        border-radius: 10px;
        padding: 50px 70px 50px 110px;
        overflow: hidden;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: start;
        justify-content: start;

        @include xxxxl-screen {
            padding: 50px 70px 50px;
        }

        @include xxl-screen {
            padding: 50px;
        }

        @include xl-screen {
            padding: 40px;
        }

        @include lg-screen {
            padding: 40px 30px;
        }

        @include md-screen {
            padding: 40px 90px;
        }

        @include sm-screen {
            padding: 30px 20px;
        }

        .logo {
            margin: 0 auto 20px;
        }

        &.login {
            @media (min-width: 1700px) {
                padding-top: 130px;
            }
        }
    }

    &__form {

        .form-group {
            .form--check .form-check-label {
                .link {
                    font-weight: 500;
                }
            }
            .btn {
                font-weight: 700;
            }
        }

        .btn {
            @include fs-20;
            padding: 20px 15px;

            @include xl-screen {
                @include fs-18;
                padding: 17px 15px;
            }

            @include lg-screen {
                @include fs-16;
                padding: 15px;
            }

            @include sm-screen {
                @include fs-15;
                padding: 14px 15px;
            }

            @include xsm-screen {
                @include fs-14;
                padding: 13px 15px;
            }

            .icon {
                max-width: 24px;

                @include xxxxl-screen {
                    max-width: 22px;
                }

                @include lg-screen {
                    max-width: 18px;
                }

                @include xsm-screen {
                    max-width: 16px
                }
            }
        }

        .have-account {
            &__text {
                @include fs-16;

                @include lg-screen {
                    @include fs-15;
                }

                @include md-screen {
                    @include fs-14;
                }

                @include msm-screen {
                    @include fs-13;
                }
            }

            &__link {
                font-weight: 500;
            }
        }

        .other-option {
            margin: 30px 0 30px;
            position: relative;
            text-align: center;
            z-index: 1;

            @include xl-screen {
                margin: 25px 0;
            }

            @include sm-screen {
                margin: 20px 0;
            }

            &::before {
                content: "";
                position: absolute;
                top: 50%;
                left: 0;
                width: 100%;
                height: 1px;
                transform: translateY(-50%);
                border-bottom: 1px solid var(--border-color);
                z-index: -1;
            }

            &__text {
                @include fs-20;
                display: inline-block;
                padding: 0 16px;
                background-color: hsl(var(--white));
                color: var(--border-color);

                @include xl-screen {
                    @include fs-18;
                }

                @include xl-screen {
                    @include fs-16;
                }
                
    
                @include lg-screen {
                    @include fs-15;
                }
    
                @include msm-screen {
                    @include fs-14;
                }
            }
        }

        .remember-forgot {
            @extend .flex-between;
            margin: 10px 0 25px;

            @include xxxl-screen {
                margin: 5px 0 20px; 
            }

            @include xl-screen {
                margin: 5px 0 20px; 
            }

            .form--check .form-check-label,
            .forgot-password {
                    @include fs-18;

                    @include xxl-screen {
                        @include fs-16;
                    }

                    @include lg-screen {
                        @include fs-15;
                    }

                    @include md-screen {
                        @include fs-14;
                    }

                    @include msm-screen {
                        @include fs-13;
                    }
                }
        }
    }
}

/* ============================================ Account Css End ====================================*/