/* =========================== Banner Section Start Here ========================= */
.banner-section {
    position: relative;
    z-index: 1;
    padding-top: 215px;

    @include xxl-screen {
        padding-top: 190px;
    }

    @include xl-screen {
        padding-top: 175px;
    }

    @include lg-screen {
        padding-top: 140px;
    }

    @include md-screen {
        padding-top: 110px;
    }

    @include msm-screen {
        padding-top: 100px;
    }

    @include xsm-screen {
        padding-top: 90px;
    }

    // &::after {
    //     position: absolute;

    // }

    &.bg-img {
        background-position: right bottom !important;

        @include md-screen {
            background-position: center right;
            object-fit: contain;
        }
    }
}

.banner-content {
    position: relative;
    z-index: 2;

    &__title {
        position: relative;
        max-width: 975px;
        margin: 0 auto 35px;

        @include xxl-screen {
            margin-bottom: 25px;
        }

        @include xl-screen {
            max-width: 925px;
            margin-bottom: 20px;
        }

        @include lg-screen {
            max-width: 845px;
            margin-bottom: 15px;
        }

        @include md-screen {
            max-width: 100%;
            margin-bottom: 10px;
        }

        @include sm-screen {
            margin-bottom: 5px;
        }

        @include msm-screen {
            max-width: 455px;
        }

        @include xsm-screen {
            max-width: 100%;
        }
    }

    &__desc {
        @extend .fs-20;
        line-height: 1.6;
        max-width: 640px;
        margin: 0 auto;

        @include lg-screen {
            max-width: 575px;
        }

        @include md-screen {
            max-width: 515px;
        }

        @include sm-screen {
            max-width: 485px;
        }

        @include msm-screen {
            max-width: 420px;
        }
    }
}

.banner-screenshot {
    position: relative;
    display: inline-block;
    margin: 0 auto;

    @include xxxl-screen {
        max-width: 935px;
    }

    @include xxl-screen {
        max-width: 900px;
    }

    @include md-screen {
        max-width: 650px;
    }

    @include xsm-screen {}

    @include xxsm-screen {}
}


.banner-shape {
    position: absolute;

    @include msm-screen {
        display: none;
    }

    &.one {
        left: 10px;
        top: 88px;

        @include xxl-screen {
            width: 45px;
            left: 5px;
            top: 70px;
        }

        @include xl-screen {
            width: 40px;
            left: 0;
            top: 60px;
        }

        @include lg-screen {
            width: 35px;
            top: 50px;
        }

        @include md-screen {
            width: 25px;
            top: 40px;
            left: -25px;
        }

        @include sm-screen {
            width: 20px;
            top: 35px;
            left: -15px;
        }
    }

    &.two {
        right: 100px;
        top: 164px;

        @include xxl-screen {
            width: 45px;
            right: 115px;
            top: 135px;
        }

        @include xl-screen {
            width: 40px;
            right: 45px;
            top: 125px;
        }

        @include md-screen {
            width: 30px;
            right: 0;
            top: 90px;
        }

        @include sm-screen {
            width: 20px;
            right: 0;
            top: 45px;
        }
    }

    &.three {
        right: -21%;
        top: 52px;

        @media (max-width: 1799px) {
            right: -17%;
        }

        @include xxxxl-screen {
            right: -13%;
        }

        @include xxxl-screen {
            right: -10%;
        }

        @include xxl-screen {
            width: 16px;
            right: -6%;
        }

        @include xl-screen {
            width: 14px;
            right: -40px;
        }

        @include lg-screen {
            right: -15px;
        }

        @include md-screen {
            right: -20px;
            width: 12px;
            top: 15px;
        }

        @include sm-screen {
            right: -15px;
            top: -7px;
            width: 10px;
        }
    }

    &.four {
        left: -120px;
        top: 85px;

        @include xxl-screen {
            width: 40px;
            left: -90px;
            top: 70px;
        }

        @include xl-screen {
            width: 30px;
            left: -60px;
            top: 55px;
        }

        @include lg-screen {
            left: -30px;
        }

        @include md-screen {
            width: 20px;
            left: -10px;
            top: 15px;
        }

        @include sm-screen {
            width: 15px;
            left: -5px;
            top: 10px;
        }
    }
}

/* =========================== Banner Section End Here ========================= */