/* =========================== Accordion Css Start ============================= */
.custom--accordion {
    .accordion-item {
        border: 0 !important;
        border-bottom: 1px solid hsl(var(--black) / .1) !important;
        background-color: transparent !important;
        border-radius: 0;
        overflow: hidden;

        &:not(:last-child) {
            margin-bottom: 20px;

            @include md-screen {
                margin-bottom: 15px;
            }
        }
    }

    .accordion-header {
        font-weight: 600;
        line-height: 1;

        @include md-screen {
            @include fs-15;
        }
    }

    .accordion-body {
        @extend .fs-16;
        padding: 0 0 25px;
        background-color: transparent;

        @include md-screen {
            @include fs-14;
            padding-bottom: 15px;
        }
    }

    &:first-of-type .accordion-button.collapsed {
        border-radius: 5px;
    }

    &:last-of-type .accordion-button.collapsed {
        border-radius: 5px;
    }

    .accordion-button {
        background-color: transparent;
        color: inherit;
        padding: 0 25px 20px 0;
        font-weight: inherit;

        @include md-screen {
            padding-bottom: 15px;
        }

        &:hover {
            color: hsl(var(--base));
        }

        &::after {
            background-image: none;
        }

        &:focus {
            box-shadow: none;
        }

        &:not(.collapsed) {
            color: inherit;
            background-color: transparent !important;
            box-shadow: none;

            &::after {
                background-image: none;
            }
        }

        &[aria-expanded="true"],
        &[aria-expanded="false"] {
            &::after {
                font-family: "Line Awesome Free";
                font-weight: 900;
                content: "\f106";
                display: inline-block;
                color: inherit;
                position: absolute;
                @extend .top-center-extend;
                top: 0;
                right: 0;
                height: unset;
                transform: translateY(0);

                @include md-screen {
                    @include fs-12;
                    top: 2px;
                }
            }
        }

        &[aria-expanded="false"] {
            &::after {
                content: "\f107";
                color: hsl(var(--heading-color));
            }
        }
    }
}

/* ================================= Accordion Css End =========================== */