/* ============= Footer Start ======================= */
.footer-area {
    position: relative;
    overflow: hidden;
    background-color: hsl(147, 69%, 3%);
    margin-top: auto;

    &__shape {
        position: absolute;
        left: 50px;

        @include xl-screen {
            left: 30px;
            max-width: 145px;
        }

        @include lg-screen {
            left: 25px;
            max-width: 125px;
        }

        @include md-screen {
            left: 15px;
            max-width: 100px;
        }

        @include msm-screen {
            left: 10px;
            max-width: 70px;
        }

        &.two {
            left: auto;
            right: 0;

            @include xl-screen {
                max-width: 100px;
            }

            @include lg-screen {
                max-width: 90px;
            }

            @include md-screen {
                max-width: 75px;
            }

            @include msm-screen {
                max-width: 60px;
            }

            @include xsm-screen {
                max-width: 50px;
            }
        }
    }

    &__inner {
        padding-bottom: 100px;

        @include xl-screen {
            padding-bottom: 70px;
        }

        @include lg-screen {
            padding-bottom: 60px;
        }

        @include md-screen {
            padding-bottom: 50px;
        }
    }

    &.bg-img {
        background-size: contain;
        background-position: top center;
    }
}

.footer-item {
    position: relative;
    z-index: 2;

    &__title {
        color: hsl(var(--white));
        margin-bottom: 30px;

        @include md-screen {
            margin-bottom: 20px;
        }
    }

    &__desc {
        color: hsl(var(--white)/.7);
        @extend .fs-18;
        line-height: 1.6;
        padding-right: 15px;

        @include xl-screen {
            @include fs-16;
        }

        @include md-screen {
            @include fs-15;
        }

        @include msm-screen {
            @include fs-14;
        }

        @include lg-screen {
            padding-right: 0;
        }
    }

    .social-list {
        margin-top: 60px;

        @include lg-screen {
            margin-top: 35px;
        }

        @include sm-screen {
            margin-top: 25px;
        }
    }
}

/* Footer List Item */
.footer-menu {
    display: flex;
    flex-direction: column;

    &__item {
        display: block;
        margin-bottom: 15px;

        @include md-screen {
            margin-bottom: 10px;
        }

        &:last-child {
            padding-bottom: 0;
        }
    }

    &__link {
        color: hsl(var(--white)/.7);
        @extend .fs-18;

        @include xl-screen {
            @include fs-16;
        }

        @include md-screen {
            @include fs-15;
        }

        @include msm-screen {
            @include fs-14;
        }

        &:last-child {
            margin-bottom: 0;
        }

        &:hover {
            color: hsl(var(--base));
            text-decoration: underline;
        }
    }
}

/* Footer Contact Info */
.footer-contact-menu {
    &__item {
        color: hsl(var(--white)/.7);
        @extend .fs-18;
        margin-bottom: 15px;

        @include xl-screen {
            @include fs-16;
        }

        @include md-screen {
            @include fs-15;
            margin-bottom: 10px;
        }

        @include msm-screen {
            @include fs-14;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }
}

/* ============= Footer End ======================= */

/* ============= Bottom Footer Start ======================= */
.bottom-footer {
    background-color: hsl(143, 29%, 5%);

    &__text {
        color: hsl(var(--white)/.7);
        @extend .fs-18;

        @include xl-screen {
            @include fs-16;
        }

        @include md-screen {
            @include fs-14;
            margin-bottom: 10px;
        }

        @include msm-screen {
            @include fs-13;
        }
    }
}

/* =============Bottom Footer End ======================= */