/* ================================= Dashboard Css Start =========================== */
.dashboard {
  position: relative;

  &__right {
    width: 100%;
    margin-left: 345px;
    background-color: #f5f6fa;

    @include xxxl-screen {
      margin-left: 300px;
    }

    @include lg-screen {
      width: 100%;
      margin-left: 0;
    }
  }

  // ======================= Dashboard Header Start======================= 
  .dropdown {
    display: inline-block !important;
  }

  .dashboard-header {
    padding: 0 56px;
    background-color: hsl(var(--white));
    border-bottom: 1px solid var(--border-color-two);

    @include xxl-screen {
      padding: 0 40px;
    }

    @include xl-screen {
      padding: 0 32px;
    }

    @include lg-screen {
      padding: 0 24px;
    }

    @include sm-screen {
      padding: 0 16px;
    }

    &__inner {
      position: relative;
      padding: 15px 0;

      @include msm-screen {
        padding: 10px 0;
      }
    }

    &__right {
      gap: 20px;

      @include md-screen {
        gap: 15px;
      }
    }
  }

  /* User Info Css Start */
  .user-info {
    position: relative;
    text-align: center;

    &__button {
      position: relative;
      padding-right: 20px;
      padding-left: 25px;

      @include md-screen {
        padding-left: 15px;
      }

      @include msm-screen {
        padding-right: 15px;
        padding-left: 10px;
      }

      &::before {
        position: absolute;
        content: '';
        background: var(--border-color-two);
        height: 100%;
        width: 1px;
        left: 0;
        top: 0;
        bottom: 0;
        margin: auto 0;
      }

      &::after {
        position: absolute;
        content: "\f0d7";
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        color: hsl(var(--dark-two));
        @include fs-15;
        pointer-events: none;

        @include msm-screen {
          @include fs-14;
        }

        @include xsm-screen {
          @include fs-13;
        }
      }

      .thumb {
        width: 50px;
        height: 50px;
        overflow: hidden;
        border-radius: 50%;

        @include md-screen {
          width: 45px;
          height: 45px;
        }

        @include sm-screen {
          width: 40px;
          height: 40px;
        }

        @include msm-screen {
          width: 35px;
          height: 35px;
        }
      }

      .name {
        @include fs-19;
        color: hsl(var(--dark-two));
        margin-left: 15px;

        @include md-screen {
          @include fs-17;
        }

        @include sm-screen {
          @include fs-16;
          margin-left: 10px;
        }

        @include msm-screen {
          @include fs-14;
        }
      }
    }


    .user-info-dropdown {
      width: 200px;
      background-color: hsl(var(--white));
      box-shadow: var(--box-shadow-card);
      border-radius: 10px;
      padding: 0;
      overflow: hidden;
      position: absolute;
      right: 0;
      z-index: 9;
      top: 100%;
      margin-top: 16px;
      transition: 0.25s linear;
      transform: scale(.95);
      visibility: hidden;
      opacity: 0;

      &.show {
        visibility: visible;
        opacity: 1;
        transform: scale(1);
      }

      @include md-screen {
        width: 175px;
      }

      @include msm-screen {
        margin-top: 11px;
      }

      &__item {
        border-bottom: 1px solid var(--border-color-two);

        &:last-child {
          border-bottom: 0 !important;
        }
      }

      &__link {
        padding: 13px 15px !important;
        font-weight: 500;
        color: hsl(var(--dark-two));
        margin-left: 0 !important;
        width: 100%;
        text-align: left;
        @extend .flex-align;
        @include fs-16;

        @include md-screen {
          @include fs-15;
        }

        @include msm-screen {
          @include fs-14;
          padding: 11px 13px !important;
        }

        &.active {
          background-color: hsl(var(--base));
        }

        &:hover {
          background-color: hsl(var(--base)/.04);
          color: hsl(var(--base)) !important;
        }

        .icon {
          margin-right: 5px;
          @include fs-20;
          line-height: 1;

          @include md-screen {
            @include fs-18;
          }

          @include fs-16;
        }
      }
    }
  }

  // Notification
  .notification {
    position: relative;

    &__button {
      @include fs-20;
      color: hsl(var(--dark-two));

      &:hover {
        color: hsl(var(--base));
      }
    }

    &__dropdown {
      border-radius: 10px;
      overflow: hidden;
      transition: 0.25s linear;
      background-color: hsl(var(--white));
      box-shadow: var(--box-shadow-card);
      width: 300px;
      position: absolute;
      right: 0;
      z-index: 9;
      top: 100%;
      margin-top: 27px;
      transform: scale(0.95);
      visibility: hidden;
      opacity: 0;

      @include md-screen {
        margin-top: 23px;
      }

      @include sm-screen {
        margin-top: 21px;
      }

      @include msm-screen {
        width: 275px;
        margin-top: 13px;
        transform: translate(152px, 0px);
      }

      &.show {
        visibility: visible;
        opacity: 1;
        transform: scale(1);

        @include msm-screen {
          transform: translate(152px, 0px);
        }
      }

      .top {
        padding: 15px;
        border-bottom: 1px solid #e5e5e5;

        @include msm-screen {
          padding: 12px;
        }

        .caption {
          font-weight: 600;
          color: hsl(var(--dark-two));

          @include md-screen {
            @include fs-15;
          }

          @include msm-screen {
            @include fs-14;
          }
        }

        .desc {
          @include fs-13;

          @include msm-screen {
            @include fs-12;
          }
        }
      }

      .view-all-message {
        width: 100%;
        border-top: 1px solid var(--border-color-two);
        text-align: center;
        font-weight: 600;
        padding: 13px 0;
        color: hsl(var(--dark-two));

        @include md-screen {
          @include fs-15;
        }

        @include msm-screen {
          padding: 12px 0;
          @include fs-14;
          font-weight: 500;
        }

        &:hover {
          color: hsl(var(--base));
        }
      }
    }

    &__list {
      max-height: 315px;
      overflow-y: auto;

      &::-webkit-scrollbar {
        width: 6px;

        @include md-screen {
          width: 4px;
        }
      }

      &::-webkit-scrollbar-track {
        background-color: hsl(var(--base)/.25);
      }

      &::-webkit-scrollbar-thumb {
        background-color: hsl(var(--base)/.6);
        transition: .2s linear;
      }

      &::-webkit-scrollbar-thumb:hover {
        background-color: hsl(var(--base));
      }
    }

    &__item {
      border-bottom: 1px solid var(--border-color-two);

      &:last-child {
        border-color: transparent;
      }
    }

    &__link {
      width: 100%;
      padding: 15px 15px;

      @include md-screen {
        padding: 12px 15px;
        @include fs-15;
      }

      @include msm-screen {
        padding: 12px;
        @include fs-14;
      }

      &:hover {
        background: hsl(var(--base)/.04);
      }

      .text {
        display: block;
        font-weight: 500;
        color: hsl(var(--dark-two));
      }

      .time {
        @include fs-13;

        @include msm-screen {
          @include fs-12;
        }

        i {
          margin-right: 5px;
        }
      }
    }
  }

  // Search
  .dashboard-search {
    position: relative;

    @include msm-screen {
      position: initial;
    }

    &__button {
      color: hsl(var(--dark-two));
      @include fs-14;

      &:hover {
        color: hsl(var(--base));
      }

      &.change-icon {
        position: relative;
        color: hsl(var(--dark-two));

        &:hover {
          color: hsl(var(--base));
        }

        i {
          &::before {
            content: '\e925' !important;
          }
        }
      }
    }

    &__form {
      position: absolute;
      top: -10px;
      right: 30px;
      width: 350px;
      transition: 0.25s linear;
      transform: scale(0.95);
      visibility: hidden;
      opacity: 0;

      @include md-screen {
        width: 315px;
      }

      @include sm-screen {
        width: 280px;
        right: 20px;
        top: -8px;
      }

      @include msm-screen {
        width: 100%;
        right: 0;
        left: 0;
        top: 62px;
        z-index: 2;
        margin: 0 auto;
      }

      &.show {
        visibility: visible;
        opacity: 1;
        transform: scale(1);
      }

      .form-group {
        .form--control {
          @include fs-15;

          background-color: hsl(var(--white)) !important;
          box-shadow: var(--box-shadow-card);
          padding: 13px 15px 13px 55px;
          border-radius: 45px;
          border-color: var(--border-color-two);

          @include md-screen {
            padding: 12px 15px 12px 50px;
          }

          @include sm-screen {
            @include fs-14;
            padding: 11px 12px 11px 47px;
          }

          &::placeholder {
            @include fs-15;

            @include sm-screen {
              @include fs-14;
            }
          }
        }

        .search-form-btn {
          @include fs-14;
          line-height: 1;
          position: absolute;
          left: 0;
          top: 0;
          bottom: 0;
          margin: auto 0;
          padding: 5px 13px 5px 15px;

          @include sm-screen {
            @include fs-13;
            padding: 5px 10px 5px 13px;
          }

          @include msm-screen {
            padding: 5px 10px 5px 15px;
          }

          &::before {
            position: absolute;
            content: "";
            background: var(--border-color-two);
            height: calc(100% - 16px);
            width: 1px;
            right: 0;
            top: 0;
            bottom: 0;
            margin: auto 0;
          }
        }
      }
    }
  }

  // ======================= Dashboard Header End ======================= 

  // ========== Dashboard Body Start ===============
  .dashboard-body {
    position: relative;
    background: hsl(0, 0%, 97%);
    padding: 56px 35px;
    min-height: calc(100vh - 81px);

    @include xxxl-screen {
      padding: 45px 25px;
    }

    @include xl-screen {
      padding: 35px 25px;
    }

    @include lg-screen {
      padding: 25px 25px 35px;
    }

    @include sm-screen {
      padding: 20px;
    }

    @include msm-screen {
      padding: 15px;
    }

    &__title {
      @include msm-screen {
        margin-bottom: 10px;
      }
    }

    &__bar {
      &-icon {
        width: 45px;
        height: 40px;
        border-radius: 5px;
        background: hsl(var(--base));
        color: hsl(var(--white));
        @include fs-18;
        margin-bottom: 20px;
        cursor: pointer;
        @extend .flex-center;

        @include msm-screen {
          width: 40px;
          height: 35px;
          font-size: 1rem;
          margin-bottom: 10px;
        }
      }
    }
  }

  // Dashboard Card
  .dashboard-widget {
    padding: 24px;
    border-radius: 15px;
    position: relative;
    z-index: 1;
    overflow: hidden;
    background-color: hsl(var(--white));
    box-shadow: var(--box-shadow-card);
    transition: .25s ease-in-out;

    &:hover {
      transform: translateY(-6px) scale(1.01);
      box-shadow: 0px 0px 20px rgb(231 231 231 / 60%);
    }

    @media (max-width: 1799px) {
      padding: 24px 18px;
    }

    @include xxl-screen {
      padding: 20px 15px;
    }

    @include lg-screen {
      padding: 20px 30px;
    }

    @include md-screen {
      padding: 20px;
    }

    @include xsm-screen {
      flex-wrap: wrap-reverse;
      padding: 15px;
    }

    &__content {
      width: calc(100% - 110px);
      padding-right: 16px;


      @media (max-width: 1799px) {
        width: calc(100% - 100px);
        padding-right: 10px;
      }

      @include xxxxl-screen {
        width: calc(100% - 90px);
      }

      @include xxl-screen {
        width: calc(100% - 80px);
      }

      @include lg-screen {
        width: calc(100% - 70px);
      }

      @include md-screen {
        width: calc(100% - 65px);
      }

      @include sm-screen {
        width: calc(100% - 60px);
      }

      @include xsm-screen {
        width: 100%;
        padding-right: 0;
      }

      .title {
        color: hsl(216, 7%, 57%);
        @include fs-20;
        margin-bottom: 5px;
        font-weight: 400;

        @media (max-width: 1799px) {
          @include fs-18;
        }

        @include xxxl-screen {
          @include fs-17;
        }

        @include xxl-screen {
          @include fs-16;
        }

        @include md-screen {
          @include fs-15;
          margin-bottom: 3px;
        }
      }

      .number {
        margin-bottom: 0px;
        color: hsl(var(--dark-two));
      }
    }

    &__icon {
      width: 110px;
      height: 110px;
      background-color: hsl(var(--base)/.08);
      color: hsl(var(--base));
      border-radius: 15px;
      font-size: 3.75rem;

      @media (max-width: 1799px) {
        width: 100px;
        height: 100px;
        font-size: 3.125rem;
      }

      @include xxxxl-screen {
        width: 90px;
        height: 90px;
        font-size: 2.813rem;
      }

      @include xxl-screen {
        width: 80px;
        height: 80px;
        font-size: 2.5rem;
      }

      @include lg-screen {
        width: 70px;
        height: 70px;
        font-size: 2rem;
      }

      @include md-screen {
        width: 65px;
        height: 65px;
        font-size: 1.8rem;
      }

      @include sm-screen {
        width: 60px;
        height: 60px;
        font-size: 1.5rem;
      }

      @include xsm-screen {
        width: 50px;
        height: 50px;
        margin-bottom: 8px;
      }
    }
  }

  // Dashboard Bottom
  .dashboard-bottom {
    @extend .flex-between;
    align-items: flex-start;
    gap: 24px;
    margin-top: 30px;

    @media (max-width: 1799px) {
      gap: 20px;
    }

    @include xxxl-screen {
      gap: 15px;
    }

    .custom--card {
      .card-header {
        @include msm-screen {
          gap: 10px;
          margin-bottom: 10px;
        }
      }
    }

  }

  .dashboard-table {
    width: calc(100% - 525px);

    @media (max-width: 1799px) {
      width: calc(100% - 450px);
    }

    @include xxxxl-screen {
      width: calc(100% - 395px);
    }

    @include xxxl-screen {
      width: calc(100% - 375px);
    }

    @include xxl-screen {
      width: 100%;
    }
  }

  .appointment-calendar-wrapper {
    width: 500px;
    padding: 48px 24px;
    border-radius: 15px;
    background-color: hsl(var(--white));
    box-shadow: var(--box-shadow-card);

    @media (max-width: 1799px) {
      width: 430px;
      padding: 40px 18px;
    }

    @include xxxxl-screen {
      width: 375px;
      padding: 30px 15px 15px;
    }

    @include xxxl-screen {
      width: 360px;
      padding: 30px 15px 15px;
    }

    @include msm-screen {
      margin: 0 auto;
      padding: 20px 15px 10px;
    }

    @include xsm-screen {
      width: 100%;
    }
  }

  .appointment_calendar {
    border: 0;
    margin: 0;
    padding: 0;
    box-shadow: none;
    background: transparent;
    border-radius: 0;
    color: hsl(var(--dark-two));
    text-shadow: none;

    table {
      margin: 0;
      width: 100%;
      border-spacing: 2px;
      position: relative;
      padding-top: 55px;

      @include xsm-screen {
        padding-top: 40px;
      }

      caption {
        position: absolute;
        width: 100%;
        top: 0;
        color: hsl(var(--dark-two));
        text-align: center;
        padding: 3px 0 2px;

        span {
          @include fs-20;

          @media (max-width: 1799px) {
            @include fs-18;
          }

          @include md-screen {
            @include fs-16;
          }

          @include sm-screen {
            @include fs-15;
          }

          @include xsm-screen {
            @include fs-14;
          }
        }

        .larr,
        .rarr {
          float: none;
          color: hsl(var(--dark-two));
          background: hsl(0, 0%, 97%);
          border-radius: 10px;
          width: 35px;
          height: 35px;
          position: absolute;
          left: 0;
          top: 0;
          @include fs-11;
          @extend .flex-center;

          @include xsm-screen {
            width: 32px;
            height: 32px;
            @include fs-10;
          }

          &:hover {
            background: hsl(var(--base));
            color: hsl(var(--white));
          }

          &::before {
            font-family: 'appoinlab-icons';
            content: '\e903';
          }
        }

        .rarr {
          left: auto;
          right: 0;

          &::before {
            font-family: 'appoinlab-icons';
            content: '\e904';
          }
        }
      }

      thead {
        color: hsl(var(--dark-two));
        text-shadow: none;

        tr {
          th {
            text-align: center;
            color: hsl(var(--body-color));
            font-weight: 400;
            @include fs-18;
            padding: 15px 5px 20px;
            text-transform: uppercase;

            @media (max-width: 1799px) {
              @include fs-16;
            }

            @include xxxxl-screen {
              @include fs-15;
              padding: 10px 5px;
            }

            @include md-screen {
              @include fs-14;
            }

            @include xsm-screen {
              @include fs-13;
            }
          }
        }
      }

      tbody {
        color: hsl(var(--dark-two));
        text-shadow: none;

        tr {
          td {
            text-align: center;
            color: hsl(var(--body-color));
            font-weight: 400;
            border: 0;
            border-radius: 10px;
            @include fs-18;
            width: 45px;
            height: 50px;


            @media (max-width: 1799px) {
              @include fs-16;
            }

            @include xxxxl-screen {
              @include fs-15;
              height: 45px;
            }

            @include xxxl-screen {
              height: 43px;
            }

            @include md-screen {
              @include fs-14;
            }

            @include xsm-screen {
              @include fs-13;
              height: 35px;
            }

            &:hover {
              font-weight: 500;
              color: hsl(var(--white));
              background: hsl(var(--base));
              border: 0;
            }

            &.calSelectable.calSelected {
              font-weight: 500;
              color: hsl(var(--white));
              background: hsl(var(--base));
              text-shadow: none;
            }
          }
        }
      }
    }
  }


  // Date Picker Form
  .date-pick-form {
    width: 250px;

    @include lg-screen {
      width: 230px;
    }

    @include md-screen {
      width: 215px;
    }

    @include sm-screen {
      width: 200px;
    }

    @include msm-screen {
      width: 100%;
    }

    .form-group {
      position: relative;
      cursor: pointer;

      &::after {
        font-family: "appoinlab-icons";
        content: "\e902";
        position: absolute;
        font-size: 0.438rem;
        right: 8px;
        top: 20px;

        @include md-screen {
          font-size: 0.4rem;
          right: 7px;
          top: 18px;
        }
      }

      .form--control {
        background: transparent !important;
        border-color: var(--border-color-two);
        padding: 13px 25px 13px 35px;
        color: hsl(var(--dark-two));

        @media (max-width: 1799px) {
          @include fs-16;
        }

        @include xxxxl-screen {
          @include fs-15;
          padding-left: 40px;
        }

        @include md-screen {
          @include fs-14;
          padding: 12px 20px 12px 32px;
        }

        @include xsm-screen {
          @include fs-13;
        }

        &::placeholder {
          @media (max-width: 1799px) {
            @include fs-16;
          }

          @include xxxxl-screen {
            @include fs-15;
          }

          @include md-screen {
            @include fs-14;
          }

          @include xsm-screen {
            @include fs-13;
          }
        }
      }

      .icon {
        color: hsl(var(--dark-two));
        position: absolute;
        left: 15px;
        top: 16px;
        line-height: 1;

        @include md-screen {
          @include fs-14;
          left: 10px;
          top: 15px;
        }
      }
    }
  }

  .datepickers-container .datepicker .datepicker--content .datepicker--day-name {
    color: hsl(var(--base));
  }

  .datepickers-container .datepicker .datepicker--content .datepicker--cell.-current-,
  .datepickers-container .datepicker .datepicker--content .datepicker--cell.-selected- {
    color: hsl(var(--white));
    background-color: hsl(var(--base)/0.9);
  }

  .datepickers-container .datepicker .datepicker--content .datepicker--cell:hover {
    color: hsl(var(--white));
    background-color: hsl(var(--base)/0.9);
  }

  /* ================================= Dashboard Css End =========================== */
}