/* ================================= Social Icon Css Start =========================== */
.social-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    &__item {
        margin-right: 10px;

        &:last-child {
            margin-right: 0;
        }
    }

    &__link {
        width: 28px;
        height: 28px;
        border-radius: 7px;
        position: relative;
        overflow: hidden;
        transition: .3s;
        cursor: pointer;
        color: hsl(0, 0%, 42%);
        background: hsl(150, 13%, 12%);
        @include fs-13;
        text-align: center;
        line-height: 29px;

        &::after {
            position: absolute;
            content: '';
            background: hsl(var(--base));
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            transition: .2s linear;
            visibility: hidden;
            opacity: 0;
        }

        i {
            position: relative;
            z-index: 2;
        }

        &.active,
        &:hover,
        &:focus {
            color: hsl(var(--white)) !important;

            &::after {
                visibility: visible;
                opacity: 1;
            }
        }

        &.facebook {
            &.active,
            &:hover,
            &:focus {
                &::after {
                    background: hsl(216, 100%, 45%);
                }
            }
        }

        &.twitter {
            &.active,
            &:hover,
            &:focus {
                &::after {
                    background: hsl(203, 89%, 53%);
                }
            }
        }

        &.linkedin {
            &.active,
            &:hover,
            &:focus {
                &::after {
                    background: hsl(201, 100%, 35%);
                }
            }
        }

        &.instagram {
            &.active,
            &:hover,
            &:focus {
                &::after {
                    background: linear-gradient(69deg, rgba(250,126,30,1) 0%, rgba(214,41,118,1) 57%, rgba(150,47,191,1) 100%);
                }
            }
        }
    }
}

/* ================================= Social Icon Css End ===========================  */